import React, { Component } from 'react';
import axios from 'axios';
import logo from './logo.svg';
import './App.css';
import { Select } from 'antd';
import { Row, Col } from 'antd';
import { Button } from 'antd';
const Option = Select.Option;

const data = require('./data.json');

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      data: [],
      location: "inside-dhaka",
      area: "Mirpur DOHS",
      areas: [],
      weights: ["1kg", "2kg", "3kg"],
      cod: "Please select the options",
      pricing: "Undefined"
    }
  }

  componentWillMount(){
    this.setState({
      data: data
    })
    // let _this = this;
    // axios.get("https://raw.githubusercontent.com/manashmndl/slate-text-editor/master/data.json").then((data) => {
    //   _this.setState({data: data.data})
    // })
  }

  render() {
    return (
      <div className="App">
        <h1>Delivery Price Calculator</h1>
        <br></br>
        <div>
          <Row gutter={16}>
            <Col className="selectLocation" span={6}>
                <Select showSearch
                placeholder="Select a location"
                onChange={(value) => this.setState({location: value, areas: Array.from(new Set( this.state.data[value].locations ))   })}
                style={{width: 200}}
                >
                  { Object.keys(this.state.data).map((l, idx) => <Option key={`${l}-${idx}`} value={l}>{l}</Option> ) }
                </Select>
            </Col>

            <Col className="selectArea" span={6}>
            <Select showSearch
                placeholder="Select an Area"
                onChange={(value) => { this.setState({area: value })}}
                style={{width: 200}}
                >
                
                  { this.state.areas.map((l, idx) => <Option value={l} key={`${l}-${idx}`}>{l}</Option> ) }
                </Select>
            </Col>

            <Col className="selectWeight" span={6}>
            <Select showSearch
                placeholder="Select Weight"
                onChange={(value) => { this.setState({weight: value })}}
                style={{width: 200}}
                >
                  { this.state.weights.map((w, idx) => <Option value={w} key={`${w}-${idx}`}>{w}</Option> ) }
                </Select>
            </Col>

            <Col>
                <Button onClick={()=> {
                    this.setState({
                      cod: this.state.data[this.state.location].cod,
                      pricing: this.state.data[this.state.location].pricing[this.state.weight]
                    })
                }}>Calculate</Button>
            </Col>
          </Row>
                <br></br>
          <Row gutter={16}>
            <Col span={16}>
                <h1>{this.state.cod}</h1>
                <br></br>
                <h1>{this.state.pricing}</h1>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}

export default App;
